.formulario-container {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    min-height: 500px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin: 0 auto;
    background-image: url(../img/hero-bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

.formulario {
    width: 95%;
    padding: 20px;
    border-radius: 20px;
    text-align: center;
    margin: 0 auto;
    color: #f4f6fb;
}

.formulario h1 {
    color: white;
    /* -webkit-text-stroke: 2px #f4f6fb; */
}

.formulario h3 {
    background: rgba(46, 72, 118, 75%);
    border-radius: 22px;
    border: 2px solid #293f63;
    padding: 5px;
}

.formulario-p {
    background: rgba(46, 72, 118, 75%);
    padding: 10px;
    border-radius: 22px;
    border: 2px solid #293f63;
}

.text {
    font-size: 22px;
    color: #f4f6fb;
    font-weight: 600;
}

.formulario-buttons {
    display: flex;
}

.buttons-question {
    flex: 1;
    appearance: none;
    border: 0.125em solid #1A1A1A;
    border-radius: 22px;
    box-sizing: border-box;
    background: rgba(246, 248, 255, 68%);
    /* color: #3B3B3B; */
    display: inline-block;
    font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
    margin: 5px;
    min-height: 2.75em;
    min-width: 0;
    outline: none;
    padding: 10px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    will-change: transform;
}

.buttons-question:disabled {
    pointer-events: none;
}

.buttons-question:hover {
    color: #65D7B7;
    background-color: #12176B;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
}

.buttons-question:active {
    box-shadow: none;
    transform: translateY(0);
}